@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: rgba(255, 255, 255, 0.7);
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* swiper-slider */

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}

.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}

.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}

.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}

.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}

.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

/* global styles*/

body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.27;
  color: #483C08;
  background-color: #fff;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 0 15px;
  border: solid 1px red;
}

.title {
  margin: 0 auto;
  font-weight: 700;
  font-size: clamp(32px, 1.1071rem + 4.4643vw, 52px);
  text-align: center;
}

/* Header styles */

header {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

.burger {
  display: none;
  width: 24px;
  height: 16px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.burger:hover .burger__span {
  background-color: blue;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: red;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

.hero {
  position: relative;
  width: 100%;
  padding: 156px 0 100px 0;
  background-color: #fff;
  background-image: url("../img/hero-bg.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.hero__overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.hero__title {
  position: relative;
  max-width: 578px;
  color: #F9E994;
  z-index: 2;
}

.hero__inner {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  z-index: 2;
  border: solid 1px red;
}

.hero__bg {
  width: 100%;
  max-width: 710px;
  position: absolute;
  left: 50%;
  z-index: 3;
  pointer-events: none;
}

.hero__bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__content {
  position: relative;
  width: 100%;
  max-width: 439px;
  padding-top: 34px;
  z-index: 2;
}

.hero__content-text {
  font-weight: 700;
  line-height: 1.3;
  color: #f6f7e4;
  text-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
  margin-bottom: 60px;
}

.hero__list {
  width: 100%;
  max-width: 323px;
  background-color: #7fb3e1;
  border-radius: 20px;
  list-style: none;
}

.hero__list-item {
  position: relative;
  padding-left: 35px;
  font-weight: 700;
  font-size: 22px;
  color: #dfe4de;
}

.hero__list-item:not(:last-child) {
  margin-bottom: 14px;
}

.hero__list-item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  translate: 0 -50% 0;
  width: 18px;
  height: 24px;
  background-image: url("../img/black-paw.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.order {
  position: relative;
  width: 100%;
  max-width: 270px;
  padding-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  background: #fdfcfc;
  opacity: 0.7;
  overflow: hidden;
  z-index: 2;
}

.order__top {
  width: 100%;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  margin-bottom: 21px;
}

.order__top-inner {
  width: 100%;
  padding: 23px 24px 28px 24px;
  background-color: #7fb3e1;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 100%, 0 90%);
}

.order__title {
  font-weight: 700;
  font-size: 33px;
  text-align: center;
  color: #483c08;
  text-transform: uppercase;
  margin-bottom: 4px;
}

.order__time {
  font-weight: 700;
  font-size: 36px;
  color: #f6e791;
  text-align: center;
}

.order__discount {
  width: 100%;
}

.order__old-price {
  font-weight: 700;
  font-size: 30px;
  color: #6b6b6b;
  margin-right: 15px;
  position: relative;
}

.order__old-price::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: 0 -50% 0;
  rotate: 163deg;
  width: 100%;
  height: 2px;
  background-color: #F6BE91;
}

.order__new-price {
  font-weight: 700;
  font-size: 36px;
  color: #483c08;
}

.order-form {
  width: 100%;
  padding: 0 24px;
}

.order-form__input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin-bottom: 15px;
}

.order-form__input-box::before {
  content: "";
  position: absolute;
  left: 16px;
  top: 50%;
  translate: 0 -50% 0;
  width: 24px;
  height: 24px;
  background-image: url("../img/grey-paw.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.order-form__input {
  width: 100%;
  height: 100%;
  padding: 0 10px 0 55px;
  border-radius: 30px;
  background-color: rgba(243, 239, 239, 0.9);
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
  color: #777;
}

.order-form__input::placeholder {
  font-weight: 700;
  font-size: 16px;
  color: #777;
}

.order-form__button {
  width: 100%;
  padding: 7px 63px;
  margin-bottom: 30px;
  border-radius: 30px;
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
  color: #fff;
  background-color: #A1D337;
}

.order__date {
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  text-align: center;
  color: #444;
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.manufacturer__title {
  background: linear-gradient(180deg, #f9e994 0%, #537d13 56.25%, #483c08 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.manufacturer__inner {
  width: 100%;
  min-height: 439px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.manufacturer__card {
  width: 100%;
  max-width: 305px;
  min-height: 439px;
  padding: 15px;
  border-radius: 20px;
  background-color: #F6E791;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.manufacturer__card-img {
  max-width: 100%;
}

.manufacturer__card-content {
  width: 100%;
  min-height: 218px;
  padding: 21px 16px 23px;
  border-radius: 20px;
  background-color: rgba(161, 211, 55, 0.4);
}

.manufacturer__card-title {
  font-weight: 700;
  color: #fff;
  text-align: center;
  margin-bottom: 19px;
}

.manufacturer__card-text {
  text-align: center;
  color: #483C08;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.3);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 320px) {
  .hero__inner {
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding-top: clamp(180px, 0.0893rem + 55.8036vw, 430px);
  }

  .hero__bg {
    translate: -50% 0 0;
    top: 20px;
  }

  .hero__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero__content-text {
    margin-bottom: 20px;
    font-size: 20px;
    text-align: center;
  }

  .hero__list {
    padding: 45px 10px 27px 27px;
  }

  .hero__list-item {
    font-size: 20px;
  }

  .manufacturer {
    width: 100%;
    padding: 50px 0;
  }

  .manufacturer__title {
    margin-bottom: 30px;
  }

  .manufacturer__inner {
    flex-direction: column;
  }

  .manufacturer__card-title {
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  .hero__inner {
    flex-direction: row;
  }

  .hero__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .hero__content-text {
    margin-bottom: 60px;
    font-size: 26px;
    text-align: left;
  }

  .hero__list {
    padding: 45px 30px 27px 44px;
  }

  .hero__list-item {
    font-size: 20px;
  }

  .manufacturer {
    padding: 100px 29px;
  }

  .manufacturer__title {
    margin-bottom: 60px;
  }

  .manufacturer__inner {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .manufacturer__card-title {
    font-size: 22px;
  }
}

@media (min-width: 1140px) {
  .hero__inner {
    padding-top: 0;
  }

  .hero__bg {
    translate: -46% -98% 0;
    top: 100%;
  }
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }

  .untranslate {
    transform: translateX(0);
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .span-three-transform {
    display: none;
  }
}